import Cookies from 'js-cookie';

const linkBackEnd = process.env.REACT_APP_API_URL;

export const chamarBackEnd = async (metodo, caminho, corpo) => {

    const tokenAutenticacao = Cookies.get("X-JWT-Token");

    if(corpo){
        return await fetch(linkBackEnd + caminho, {
            method: metodo,
            headers: { 
                "Content-Type": "application/json",
                "X-JWT-Token": tokenAutenticacao
                },
            body: JSON.stringify(corpo),
            }).then((resposta) =>  {
            return resposta
        })
    }else{
        return await fetch(linkBackEnd + caminho, {
            method: metodo,
            headers: { 
                "Content-Type": "application/json",
                "X-JWT-Token": tokenAutenticacao
                },
            }).then((resposta) =>  {
                return resposta
            }).catch((error)=>{
                return error
            })
        }
  };